import { AddGroup } from './GroupModels';
// import moment from 'moment';

export const AddGroupData = (data: AddGroup) => {
	const today = new Date() 
	const eff_date = data['effectivity_date'] ?
		new Date(new Date(data['effectivity_date']).setHours(0, 0, 0, 0)) :
		undefined
	const exp_date = data['expiry_date'] ?
		new Date(new Date(data['expiry_date']).setHours(0, 0, 0, 0)) :
		undefined

    console.log('DATES : ', today, eff_date, exp_date);
    if (exp_date && eff_date)
		console.log('BOOLS', eff_date <= today, exp_date <= today);


        
    const payload: AddGroup = {
        _id: data['_id'],
        name: data['name'],
        description: data['description'],
        department: data['department'],
        effectivity_date: data['effectivity_date'],
        expiry_date: data['expiry_date'],
        reports_to: data['reports_to']
    }

    return payload;
}

export const EmptyAddGroup = () => {
    const payload: AddGroup = {
        _id: '',
        name: '',
        description: '',
        department: '',
        effectivity_date: '',
        expiry_date: '',
        reports_to: ''
    }

    return payload;
}