import React from 'react';
import { getGroups } from 'Pages/API/Calls/Group';
import {
    Grid,
    // InputLabel, 
    // Typography,
    Link,
    // Button
} from '@material-ui/core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { SideModalBody } from './Components/SideModalBody';
// import { SideModal } from './Comopnents/SideModal';
import {
    faPlus,
    faDownload,
    // faLandmark
} from '@fortawesome/free-solid-svg-icons';
import { ReactTable } from 'Components/UI/TableComponent/ReactTable';
import { ReactSearch } from 'Components/UI/SearchBar/ReactSearch';
import { Redirect } from 'react-router-dom';
import { Loader } from 'Pages/Lookup/Components/Loader';

// Imports for Add Group
import { SideModalBlock } from 'Components/UI';
import { EmptyAddGroup } from './ConstructPayloads';
import { AddGroup } from './GroupModels';

interface IProps {
    classes: any;
}

interface IState {
    rows: any;
    columns: any;
    loading: boolean;
    redirect_to_group: boolean;
    redirect_to_addgroup: boolean;
    redirect_data: any;
    add_group?: AddGroup;
    invalid_fields: string[];
    show_side_modal_block?: string;
    disableSaveButton: boolean;
    clickSaveButton: boolean;
    side_modal_block_mode: string;
    totalCount: number;
    currentPage: number;
    limit: number;
    order: string;
    typingTimeout: any;
    default_filter: any;
    query: string;
    skip: number;
    modalTitle: string;
    modalMessage: string;
    isModalOpen: boolean;
    confirm_modal_message: string;
    confirm_modal_open: boolean;
    confirm_modal_title: string;
    action_after_confirm: () => void;
    department_dropdown_choices: any[];
    reports_to_dropdown_choices: any[];
}

export class GroupsTabPage extends React.Component<IProps, IState> {

    constructor(props) {
        super(props);

        const columns = [
            {
                name: 'group_id',
                title: 'Group ID'
            },
            {
                name: 'name',
                title: 'Name'
            },
            {
                name: 'roles',
                title: 'Attached Roles'
            },
            {
                name: 'department',
                title: 'Department'
            },
            {
                name: 'status',
                title: 'Status'
            },
        ];

        this.state = {
            loading: true,
            columns: columns,
            rows: [],
            redirect_to_group: false,
            redirect_to_addgroup: false,
            redirect_data: [],
            totalCount: 0,
            currentPage: 0,
            typingTimeout: 0,
            limit: 10,
            order: '',
            default_filter: [],
            query: '',
            skip: 0,
            add_group: EmptyAddGroup(),
            invalid_fields: [],
            show_side_modal_block: 'none',
            disableSaveButton: false,
            clickSaveButton: false,
            side_modal_block_mode: 'add',
            modalTitle: '',
            modalMessage: '',
            isModalOpen: false,
            confirm_modal_message: '',
            confirm_modal_open: false,
            confirm_modal_title: '',
            action_after_confirm: () => { },
            department_dropdown_choices: props.departmentDropdown,
            reports_to_dropdown_choices: props.reportsToDropdown
        }
    }

    componentDidMount() {
        let group = this;
        this.setState({
            loading: true,
        }, () => {
            group.APIGetGroupList(this.state.currentPage, this.state.limit, this.state.query);
        });
    }

    APIGetGroupList = (page: number, limit: number, query: string) => {
        this.setState({
            loading: true,
        })

        let skip = page * limit

        this.setState({
            currentPage: page
        }, () => {
            this.setState({
                skip: skip
            })
        })

        let filter = {
            limit: limit,
            skip: skip
        }

        getGroups(filter, this.state.default_filter, this.state.order, query).then(res => {
            console.log((Math.ceil(res.count / limit) - 1))
            console.log('this is the response: ', res.body)
            this.setState({
                rows: res.body,
                loading: false,
                totalCount: res.count,
                currentPage: this.state.currentPage > (Math.ceil(res.count / limit) - 1) ? (Math.ceil(res.count / limit) - 1) : page,
                limit: limit
            })
        }).catch(() => {
            this.setState({
                rows: [],
                loading: false,
                totalCount: 0,
            });
        })
    }

    handleTimeOutSearchTableGroups = (data) => {
        if (this.state.typingTimeout) {
            clearTimeout(this.state.typingTimeout);
        }
        this.setState({
            currentPage: 0
        }, () => {
            this.setState({
                query: data,
                typingTimeout: setTimeout(() => {
                    this.setState({
                        loading: true
                    })
                    this.APIGetGroupList(
                        this.state.currentPage,
                        this.state.limit,
                        this.state.query);
                }, 3000)
            })
        })
    }

    onSearchTableGroups = () => {
        if (this.state.typingTimeout) {
            clearTimeout(this.state.typingTimeout)
        }

        this.setState({
            loading: true,
            currentPage: 0
        }, () => {
            this.APIGetGroupList(0, this.state.limit, this.state.query);
        })
    }

    handleOnFilterChange = (filter) => { 
        let _filter: any = {};

        if (this.state.typingTimeout) {
            clearTimeout(this.state.typingTimeout)
        }

        filter.map(w =>

            w.columnName === 'status' ?
                w.value !== 'all' ?
                    _filter[w.columnName] = `${w.value}` : ''
                : w.columnName === 'group_id' ?
                    _filter['group_id'] = `${w.value}`
                :w.columnName === 'roles' ?
                    _filter['roles.name'] = {
                        like: `${w.value}`,
                        options: 'i'
                    }
                : _filter[w.columnName] = {
                    like: `${w.value}.*`,
                    options: 'i'
                }
            

        )

        this.setState({
            default_filter: _filter,
            currentPage: 0,
            typingTimeout: setTimeout(() => {
                this.setState({
                    loading: true
                })
                this.APIGetGroupList(
                    this.state.currentPage,
                    this.state.limit,
                    this.state.query
                );
            }, 1500)
        })
    }

    handleSortChange = (sort) => {
        this.setState({
            order: `${sort[0].columnName} ${sort[0].direction.toUpperCase()}`,
            loading: true,
        },
            () => {
                this.APIGetGroupList(
                    this.state.currentPage,
                    this.state.limit,
                    this.state.query
                )
            })
    }

    handleRowClick = (value) => {
        const data = this.state.rows
        console.log(`this is the data `, data[value].group_id)
        this.setState({
            redirect_to_group: true,
            redirect_data: data[value].group_id
        })
        console.log(`this is the data: `, this.state.redirect_data)
    }

    handleAddGroup = () => {
        console.log('Loading: Add Group Handler');
    }
 
    rowDataFixer = (array: any) => {
        let newEntry: any = [];
        if (array.length > 0) {
            array.forEach(data => {
                let newRow = {
                    ...data,
                    name: data.hasOwnProperty('name') ? data.name : 'N/A',
                    group: data.hasOwnProperty('group_id') ? data.group_id : 'N/A',
                    department: data.hasOwnProperty('department') ? data.department : 'N/A',
                    roles: data.hasOwnProperty('roles') && data.roles.length > 0 ?
                        data.roles.map((x, i) => `${x.name}${i !== (data.roles.length - 1) ? ', ' : ''}`) : 'N/A',
                    status: data.hasOwnProperty('status') ? <span style={{ textTransform: 'capitalize' }}>{data.status}</span> : 'N/A',
                }

                newEntry.push(newRow)
            })
            return newEntry
        }
        return []
    }

    /**** SIDE MODAL FUNCTIONS ****/
    handleCloseSideModalBlock = () => {
        if(this.state.typingTimeout) {
            clearTimeout(this.state.typingTimeout);
        }
        this.setState({
            show_side_modal_block: 'none',
            side_modal_block_mode: '',
            add_group: EmptyAddGroup(),
            currentPage: 0
        }, () => {
            this.setState({
                typingTimeout: setTimeout(() => {
                    this.setState({
                        loading: true
                    })
                    this.APIGetGroupList(
                        this.state.currentPage,
                        this.state.limit,
                        this.state.query);
                }, 3000)
            })
        })
    };


    handleOpenBlock = () => {
        this.setState({
            show_side_modal_block: 'block',
        }, () => this.passDataToSideModalBlockBody());
    }

    handleInvalidFieldsPrompts = () => {
        this.setState({
            isModalOpen: true,
            modalTitle: 'Incomplete/Invalid Fields',
            modalMessage: 'Please complete the form before proceeding.'
        })
    }

    handleSaveGroup = (groupData: any) => {
        console.log(`this is the group data from the modal: `, groupData)


    }

    passDataToSideModalBlockBody = () => {
        if (this.state.add_group) {
            this.setState({
                add_group: this.state.add_group
            })
        }
    }



    handleChangeSideModalBlockMode = () => {
        this.setState({
            side_modal_block_mode: 'add'
        }, () => this.handleOpenBlock());
    }



    /****  RENDER ADD MODAL ****/

    renderSideModalBlock(): JSX.Element {
        const { classes } = this.props;

        return (
            <SideModalBlock
                blockPosition={'fixed'}
                title={'Add Group'}
                editIconDescription={'Add Group'}
                showBlock={this.state.show_side_modal_block}
                handleClose={this.handleCloseSideModalBlock}
                sideModalBlockMode={'add'}
            >
                {/* <SideModal
                    formFields={this.state.add_group}
                    invalidFields={this.state.invalid_fields}
                    classes={this.props.classes}
                    handleCLose={this.handleCloseSideModalBlock}
                    refreshData={(data: any) => {
                        this.setState({add_group: data},
                            () => this.checkFields(data, true))
                    }}
                    handleSave={(formFields?: any) => {
                        if(this.state.side_modal_block_mode) {
                            this.setState({ clickSaveButton: true }, () => this.checkFields(formFields))
                        }
                    }}
                    formName={'Add Group'}
                    mode={this.state.side_modal_block_mode ? this.state.side_modal_block_mode : ''}
                    checkBoxWithDropDownFieldsInitState={false}
                    dynamicDropdownChoices={this.state.department_dropdown_choices}
                    disableSaveButton={this.state.disableSaveButton}
                    datePairField1MaxDate={
                        this.state.add_group &&
                          this.state.add_group['expiry_date'] &&
                          moment(this.state.add_group['expiry_date']).isValid() ?
                          moment(this.state.add_group['expiry_date']).format('MM-DD-YY').toString() : undefined
                      }
                      datePairField2MinDate={
                        this.state.add_group &&
                          this.state.add_group['effectivity_date'] &&
                          moment(this.state.add_group['effectivity_date']).isValid() ?
                          moment(this.state.add_group['effectivity_date']).format('MM-DD-YY').toString() : undefined
                      }
                /> */}
                <SideModalBody
                    classes={classes}
                    handleClose={this.handleCloseSideModalBlock}
                />
            </SideModalBlock>
        )
    }


    render(): JSX.Element {
        if (this.state.redirect_to_group) {
            return (
                <>
                    <Redirect to={{
                        pathname: '/system-admin/group/' + this.state.redirect_data,
                    }}
                    />
                </>
            )
        }


        return (
            <div>
                {this.state.loading ? <Loader /> : null}
                <Grid container alignItems={'center'} style={{ textAlign: 'center' }} spacing={4}>
                    <Grid item xs={8} md={9}>
                        <ReactSearch
                            placeholder={'Search a group'}
                            onSearch={this.onSearchTableGroups}
                            onChange={this.handleTimeOutSearchTableGroups}
                        />
                    </Grid>
                    <Grid item xs={4} md={3}>
                        <Grid container>
                            <Grid item xs={6}>
                                <FontAwesomeIcon
                                    icon={faDownload}
                                    className={this.props.classes.addFieldIcone}
                                />
                                <Link className={this.props.classes.linkStyleSelected}>Generate Report
                                </Link>
                            </Grid>
                            <Grid item xs={6}>
                                <FontAwesomeIcon icon={faPlus}
                                    className={this.props.classes.addFieldIcon} />
                                <Link
                                    className={this.props.classes.linkStyleSelected}
                                    onClick={() => {
                                        this.setState(
                                            {
                                                add_group: EmptyAddGroup(),
                                                disableSaveButton: true
                                            }, () => this.handleChangeSideModalBlockMode()
                                        );
                                    }}>
                                    Add Group
                                </Link>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
                <ReactTable
                    rows={this.rowDataFixer(this.state.rows)}
                    columns={this.state.columns}
                    message={'There are no user groups to show yet.'}
                    viewOnly={false}
                    enableSelection={false}
                    onRowClick={this.handleRowClick}
                    totalCount={this.state.totalCount}
                    currentPage={this.state.currentPage}
                    pageSize={this.state.limit}
                    defaultFilters={this.state.default_filter}
                    onFilterChange={this.handleOnFilterChange}
                    onSortingChange={this.handleSortChange}
                    onSetPageSize={(size: any) => {
                        this.APIGetGroupList(
                            this.state.currentPage,
                            size,
                            this.state.query)
                    }}
                    onPageChange={(page: any) => {
                        this.APIGetGroupList(
                            page,
                            this.state.limit,
                            this.state.query
                        )
                    }}
                />
                {this.renderSideModalBlock()}
            </div>
        )
    }



}